.profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 1140px;
    width: 100%;
    margin: 74px auto 0;
}

.profile__form {
    width: 410px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 123px;
}

.profile__info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border: none;
    border-bottom: 1px solid #424242;
    font-style: normal;
    font-size: 11px;
    line-height: 13px;
    padding-bottom: 16px;
}

.profile__info_email {
    border: none;
    padding-bottom: 0;
    padding-top: 17px;
}

.profile__name {
    font-weight: 500;
    height: 14px;
    width: 141px;
}

.profile__input {
    font-weight: 400;
    text-align: right;
    background-color: unset;
    border: none;
    height: 14px;
    color: #FFFFFF;
    width: 200px;
}

.profile__input:focus {
    outline: none;
}

.profile__button {
    margin: 0;
    margin-top: 220px;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;  
    color: #FFFFFF;
    background-color: unset;
    border: none;
    transition: .2s;
    cursor: pointer;
    width: 100%;
    text-align: center;
}

.profile__error {
    color: #ff004c;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    margin-top: 189px;
    cursor: default;
}

.profile__button_disabled {
    cursor: default;
    pointer-events: none;
}

.profile__button_span{
    margin-top: 20px;
}

.profile__button:hover {
    opacity: .8;
    transition: .2s;
}

.profile__logout {
    margin-top: 16px;
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    color: #EE3465;
    text-decoration: none;
    cursor: pointer;
    transition: .2s;
}

.profile__logout:hover {
    opacity: .8;
    transition: .2s;
}

.input__error {
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #EE3465;
    align-self: flex-start;
}

@media screen and (max-width: 770px) {
    .profile__button {
        margin-top: 210px;
    }
}

@media screen and (max-width: 440px) {
    .profile {
        margin-top: 70px;
    }

    .profile__form {
        width: 260px;
        margin-top: 80px;
    }

    .profile__button {
        margin-top: 379px;
        font-size: 12px;
        line-height: 15px; 
    }

    .profile__logout {
        font-size: 12px;
        line-height: 15px;
    }
}