.burger-btn {
    height: 44px;
    width: 44px;
    background-image: url(../../../../../../images/burger.svg);
    cursor: pointer;
    transition: .2s;
    display: none;
    position: relative;
    margin-right: 30px;
}

.burger-btn:hover {
    opacity: .8;
    transition: .2s;
}

.navi-logged__menu-bar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 13px;
    line-height: 18px;
    font-style: normal;
    font-weight: 400;
    text-align: center;
    text-decoration: none;
    margin-right: 70px;
}

.navi-logged__links {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 44px;
}

.navi-logged__item {
    text-decoration: none;
    transition: .2s;
    color: white;
    margin-right: 16px;
}

.navi-logged__item:hover {
    cursor: pointer;
    opacity: .8;
    transition: .2s;
}

.active {
    font-weight: 500;
    line-height: 18px;
}

.navi-logged__account {
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navi-logged__account_link {
    margin-right: 17px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 500;
    font-style: normal;
    color:white;
    transition: .2s;
    text-decoration: none;
}

.navi-logged__account_link:hover {
    opacity: .8;
    transition: .2s;
}

.navi-logged__account_icon {
    width: 26px;
    height: 30px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
}

@media screen and (max-width: 770px) {
    .burger-btn{
        display: block;
    }

    .navi-logged__menu-bar {
        display: none;
    }

    .navi-logged__account_link {
        display: none;
    }

    .navi-logged__account_icon {
        display: none;
    }
}

@media screen and (max-width: 321px) {
    .burger-btn {
        width: 40px;
        height: 43px;
        background-size: contain;
        background-position: center;
        margin-right: 14px;
    }
}